body {
    overflow: hidden;
}
div.contact-container {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))), url('../assets/contact-form-background.jpg') no-repeat center center fixed;
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('../assets/contact-form-background.jpg') no-repeat center center fixed;
    background: -o-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('../assets/contact-form-background.jpg') no-repeat center center fixed;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('../assets/contact-form-background.jpg') no-repeat center center fixed;
    background-size: cover;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    overflow-y: auto;
}
div.form-container {
    max-width: 600px;
    color: white;
    margin: 32px 16px 32px 16px;
    padding: 32px;
    border-style: solid;
    border-radius: 8px;
    border-width: 1px;
    border-color: white;
}
@media screen and (max-width: 700px) {
    div.form-container {
        padding: 16px;
        margin: 16px;
    }
}
div.form-container h3 {
    margin: 0;
}
div.form-container input, textarea {
    margin-top: 16px;
    font-size: 16px;
}

div.form-container input[type=submit] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-weight: bold;
    font-size: 20px;
    padding: 4px;
    width: 100px;
    height: auto;
    -ms-flex-item-align: center;
        align-self: center;
}



